.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
  background-color: #74EBD5;
  background-image: -webkit-gradient(linear, left top, right top, from(#74EBD5), to(#9FACE6));
  background-image: -webkit-linear-gradient(left, #74EBD5 0%, #9FACE6 100%);
  background-image: -o-linear-gradient(left, #74EBD5 0%, #9FACE6 100%);
  background-image: linear-gradient(90deg, #74EBD5 0%, #9FACE6 100%);
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #000;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}





